.home {
  max-width: 1440px;
  display: flex;
  justify-content: space-between;
  gap: 125px;
  padding-top: 78px;
  padding-bottom: 100px;
  max-width: 1440px;
  margin-left: auto;
}
.home_h {
  font-family: Inter;
  font-weight: 600;
  font-size: 80px;
  line-height: 82px;
  color: #191a15;
  margin-bottom: 20px;
  animation-name: changeBgColor;
  animation-duration: 3s;
}

@keyframes changeBgColor {
  0% {
    transform: scale(0.2);
    transform: translatex(-500px);
  }

  40% {
    transform: scale(1.2);
  }

  60% {
    transform: translateY(-25);
  }

  80% {
    transform: translateY(0);
  }

  to {
    transform: scale(1);
  }
}
.home_p {
  font-family: Inter;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  color: #191a15;

  margin-bottom: 40px;
}
.home_info {
  width: 50%;
  box-sizing: border-box;
}
.img {
  width: 464px;
  height: 526px;

  border-radius: 10px;
}
.home_img {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}
.home_Info {
  display: none;
}
.home_btn {
  border: 1px solid;
  border-radius: 30px;
  padding: 14px 40px;
  margin-right: 8px;
  font-family: Inter;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  background-color: #fc832c;
  color: #fbfbfb;
  cursor: pointer;
  transition: 0.3s;
}
.home_btn:hover {
  background-color: #fc832c;
  background-color: #f37113;
}
.home_price {
  background-color: #fc832c;
  position: absolute;

  border-radius: 20px;
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 32px;

  top: 510px;

  right: 250px;
  animation-name: price;
  animation-duration: 5s;
}
@keyframes price {
  0% {
    transform: scale(0.2);
    transform: translatex(500px);
  }

  40% {
    transform: scale(1.2);
  }

  60% {
    transform: translateY(-25);
  }

  80% {
    transform: translateY(0);
  }

  to {
    transform: scale(1);
  }
}
.home_svg {
  padding: 28px;
  background: #fbfbfb;
  border-radius: 13px;
}
.home_icon {
  position: absolute;
  top: 350px;
  right: 460px;
  height: 10px;

  display: flex;
  padding: 20px;
  align-items: center;
  background-color: #54be96;
  border-radius: 10px;
  transform: rotate(45deg);
  animation-name: icon;
  animation-duration: 4s;
}
@keyframes icon {
  0% {
    transform: scale(0.2);
    transform: translatex(500px);
  }

  40% {
    transform: scale(1.2);
  }

  60% {
    transform: translateY(-25);
  }

  80% {
    transform: translateY(0);
  }

  to {
    transform: scale(1);
  }
}
.home_icon_svg {
  font-size: 20px;
  color: #fbfbfb;
  transform: rotate(-45deg);
}
.home_psyhologists {
  font-family: Inter;
  font-weight: 400;
  font-size: 15px;
  line-height: 16.94px;
  color: rgba(251, 251, 251, 0.5);
}
.home_experienced {
  font-weight: 700;
  font-size: 24px;
  line-height: 29.05px;
  color: rgb(251, 251, 251);
}
.home_depths {
  font-family: Inter;
  font-weight: 600;
  font-size: 80px;
  line-height: 82px;
  color: #fc832c;
  margin-bottom: 20px;
}

@media screen and (max-width: 768px) {
  .home {
    display: inline;
  }
  .home_Info {
    display: inline;
  }
  .home_info {
    display: none;
  }
  .img {
    width: 100%;
    min-width: 320px;
    height: 100%;
  }
}
@media screen and (max-width: 935px) {
  .home_icon {
    display: none;
  }
}
